.container_user{
    display: flex;
    align-items: center;
}

.greetings{
    display: flex;
    flex-direction: column;

    color:var(--accent-color-orange);
    font-size: 12px;
    font-weight: 500;

    margin: 4px;
}

@media screen and (min-width: 420px) {
    .greetings{
        font-size: 14px;
    }
}
@media screen and (min-width: 786px) {
  .greetings {
    font-size: 18px;
  }
}

@media screen and (min-width: 1200px) {
  .greetings {
    font-size: 20px;
  }
}

.thumb_greetings{
    display: flex;
}
@media screen and (max-width: 785px) {
  .thumb_greetings {
    display:inline-block
  }
}

.user_name{
    font-weight: 700;
}

@media screen and (max-width: 785px) {
  .user_name {
    font-weight: 500;
  }
}

